.milk {
  width: 900px;
  height: 600px;
}
.CodeMirror,
.milk-dropzone,
.milk-preview,
.milk-editor-main {
  width: 100%;
  height: 100%;
}
.milk-full-dropzone {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #000;
  z-index: 10;
  opacity: 0;
}
.milk-dropzone {
  position: relative;
  z-index: 0;
}
.milk-preview {
  padding-left: 30px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: #fff;
  text-align: left;
  overflow-y: scroll;
  z-index: 2;
}



