html {
  font-size: 16px;
  font-family: Georgia;
}
.App {
  text-align: center;
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.PeterLogo {
  font-size: large;
  color: white;
}
.homePage {
  font-style: italic;
  font-weight: bold;
}
.homePage .blogAndLinks {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  margin-top: 10px;
}
.homePage .aboutThisBlog {
  -ms-flex: 1 1;
      flex: 1 1;
}
.homePage .recentBlogs {
  -ms-flex: 1 1;
      flex: 1 1;
}
.ant-layout {
  background-image: url(p5.jpg);
}
.ant-layout .ant-layout-header.headerFixed {
  position: fixed;
  height: 60px;
  width: 100%;
  z-index: 1000;
}
.ant-layout-content {
  width: 90%;
  max-width: 1400px;
  margin: 10px auto;
  border-radius: 5px;
  opacity: 0.9;
  min-height: 1080px;
  /* min-height: 400px; */
}
.ant-layout-content.contentFixed {
  padding-top: 60px;
}
.ant-layout-content img {
  max-width: 100%;
}
.navLink {
  padding-left: 25px;
  color: #b6c6e8;
  font-size: larger;
}
/* markdown */
pre {
  background: lightgray;
}
.hljs {
  background: lightgray;
}
code {
  background: #0805c2;
  font-weight: bold;
  color: red;
}
blockquote {
  background: lightgray;
}
.postAddComment,
.postComments {
  margin-top: 20px;
}
.postCommentContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.postCommentContainer .comment {
  display: -ms-flexbox;
  display: flex;
  padding: 15px 0px;
  border-bottom: solid 1px grey;
}
.postCommentContainer .comment .ant-avatar {
  margin-right: 10px;
}
.postCommentContainer .comment .commentContentContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-direction: column;
      flex-direction: column;
}
.postDetailTitle {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.postDetailTitle .postTitleInfo {
  display: -ms-flexbox;
  display: flex;
}
.postDetailTitle .postTitleInfo h1 {
  -ms-flex: 1 1;
      flex: 1 1;
}
.adminUserList {
  border-radius: 10px;
}
.adminUserList .adminUser {
  display: -ms-flexbox;
  display: flex;
  margin: 10px;
  border-bottom: solid 1px #e8e8e8;
  -ms-flex-align: center;
      align-items: center;
}
.adminUserList .adminUser div {
  -ms-flex: 1 1;
      flex: 1 1;
}
.postListContainer {
  border: 1px solid #e8e8e8;
  border-radius: 4px;
  overflow: auto;
  padding: 8px 24px;
}
.postItem {
  display: -ms-flexbox;
  display: flex;
  padding: 10px;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-direction: row;
      flex-direction: row;
  border-bottom: #b3a9a9 0.5px solid;
}
.postItem .postItemTitle {
  -ms-flex: 1 1;
      flex: 1 1;
}
.postItem .postItemAvatar {
  padding-right: 5px;
}
.postItem .postItemCategory {
  padding-right: 5px;
}
.milk-full-dropzone {
  top: 60px !important;
}
.loginForm {
  margin: 5% 35% 25% 35%;
  background: white;
  padding: 20px;
  border-radius: 10px;
}
.tableUserInfo {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}
.tableUserInfo td {
  text-align: center;
  padding: 10px;
}
@media screen and (max-width: 1024px) {
  .ant-layout-content {
    width: 100%;
    max-width: 1400px;
    margin: 5px auto;
    font-size: 1rem;
    overflow-x: hidden;
  }
  .ant-layout-content .postListContainer {
    padding: 0;
  }
  .loginForm {
    margin: 5% 10% 25% 10%;
  }
  .navLink {
    font-size: small;
    padding-left: 0.5rem;
  }
}



