.milk-buttons {
  position: absolute;
  top: 0;
  right: 10px;
  z-index: 2;
  opacity: 1;
  overflow: hidden;
}
.milk-buttons:hover > .milk-buttons-container {
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
}
.milk-buttons .milk-buttons-container {
  background-color: #f06060;
  border-radius: 0 0 30px 30px;
  -webkit-transition: -webkit-transform 0.2s;
  transition: -webkit-transform 0.2s;
  -o-transition: transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
  -webkit-transform: translateY(-88%);
      -ms-transform: translateY(-88%);
          transform: translateY(-88%);
}
.milk-buttons .milk-buttons-slide {
  overflow: hidden;
}
.milk-buttons .milk-buttons-slide > div {
  cursor: pointer;
  text-align: center;
  margin: 20px 10px;
  vertical-align: top;
}
.milk-buttons .milk-buttons-slide > div:nth-last-child(2),
.milk-buttons .milk-buttons-slide > div:nth-last-child(1),
.milk-buttons .milk-buttons-slide button {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #fff;
  background: #f06060;
  -webkit-box-shadow: 0 0 0 4px #fff;
          box-shadow: 0 0 0 4px #fff;
  width: 44px;
  height: 44px;
  border-radius: 50%;
}
.milk-buttons .milk-buttons-slide > div:nth-last-child(2) > label,
.milk-buttons .milk-buttons-slide > div:nth-last-child(1) > label,
.milk-buttons .milk-buttons-slide button > label {
  -webkit-transform: translateY(25%);
      -ms-transform: translateY(25%);
          transform: translateY(25%);
}
.milk-buttons .milk-buttons-slide > div:nth-last-child(2) > input,
.milk-buttons .milk-buttons-slide > div:nth-last-child(1) > input,
.milk-buttons .milk-buttons-slide button > input,
.milk-buttons .milk-buttons-slide > div:nth-last-child(2) > label,
.milk-buttons .milk-buttons-slide > div:nth-last-child(1) > label,
.milk-buttons .milk-buttons-slide button > label {
  vertical-align: middle;
  height: 90%;
}
.milk-buttons .milk-buttons-slide > div:nth-last-child(2),
.milk-buttons .milk-buttons-slide > div:nth-last-child(1) {
  -webkit-transition: color 0.2s, background 0.2s;
  -o-transition: color 0.2s, background 0.2s;
  transition: color 0.2s, background 0.2s;
}
.milk-buttons .milk-buttons-slide > div:nth-last-child(2):hover,
.milk-buttons .milk-buttons-slide > div:nth-last-child(1):hover {
  background: #fff;
  color: #f06060;
}
.milk-buttons .milk-buttons-slide button {
  outline: none;
  border: none;
  cursor: pointer;
  -webkit-transition: color 0.2s, background 0.2s;
  -o-transition: color 0.2s, background 0.2s;
  transition: color 0.2s, background 0.2s;
}
.milk-buttons .milk-buttons-slide button:hover {
  background: #fff;
  color: #f06060;
}
.milk-buttons .milk-buttons-drag {
  padding-top: 10px;
}
.milk-buttons .milk-buttons-drag div {
  color: #fff;
  width: 100%;
  height: 30px;
  cursor: pointer;
  text-align: center;
  vertical-align: middle;
}



